@import url(https://fonts.googleapis.com/css?family=Comfortaa|Comic+Neue&display=swap);
* {
  margin: 0;
  border: 0;
  padding: 0;
  font-family: 'Comic Neue', cursive, sans-serif;
}

.vertical-align {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.subheading {
  color: grey;
  font-size: 1rem;
}

.heading {
  font-family: "Comfortaa";
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.loading {
  position: absolute;
  /* top: 0; */
  left: 50%;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.6);
  display: flex;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.err-txt {
  font-size: 0.8rem;
  color: red;
}

.small-txt {
  font-size: 0.8rem;
  color: grey;
}
/*# sourceMappingURL=App.css.map */
.auth-div {
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.02);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.btn {
  outline: none;
  background: none;
  padding: 5px 15px;
  margin: 1rem;
  color: rgba(0, 0, 0, 0.6);
  font-size: 0.95rem;
  border-radius: 5px;
  font-weight: lighter;
  outline: none;
  cursor: pointer;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.btn:focus {
  outline: none;
  border: none;
}

.btn:hover {
  color: black;
}

.border {
  border: solid 1px rgba(0, 0, 0, 0.2);
  border-radius: 3px;
}
/*# sourceMappingURL=AuthScreen.css.map */
.MuiMobileStepper-progress {
  width: 100% !important;
}

.color-btn-img {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: rgba(22, 57, 153, 0.6);
}

.color-btn-img input {
  background-color: transparent;
  color: white;
  padding: 5px 15px;
  margin: 1rem;
  font-size: 0.8rem;
  border-radius: 5px;
  font-weight: lighter;
}
/*# sourceMappingURL=Signup.css.map */
.input-label-div {
  position: relative;
  overflow: hidden;
  margin: 10px;
  height: 2.5rem;
}

.input-label-div .inp-label {
  position: absolute;
  top: 0.5rem;
  font-size: 0.8rem;
  left: 10px;
  color: rgba(0, 0, 0, 0.5);
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}

.input-label-div .inp {
  width: 100%;
  border-radius: 5px;
  height: 100%;
  bottom: 0;
  padding-top: 2%;
  padding-left: 8px;
  font-size: 0.8rem;
  /* transition: all linear 0.1s; */
  border: solid 1px rgba(27, 9, 9, 0.1);
}

.input-label-div .inp:valid + .inp-label {
  outline: none;
  color: black;
  top: 0;
  font-size: 0.7rem;
}

.input-label-div .inp:focus + .inp-label {
  outline: none;
  color: black;
  top: 0;
  font-size: 0.7rem;
}

.color-btn {
  background-color: #160d4f;
  color: white;
  padding: 5px 15px;
  margin: 1rem;
  font-size: 0.95rem;
  border-radius: 5px;
  font-weight: lighter;
  cursor: pointer;
  -webkit-transition: all ease-in 0.3s;
  transition: all ease-in 0.3s;
  width: 80%;
}

.color-btn:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  background-color: rgba(22, 13, 79, 0.9);
  color: white;
}

.box {
  border: 1px solid rgba(0, 0, 0, 0.2);
  width: 300px;
  background-color: white;
}

@media only screen and (max-width: 600px) {
  .box {
    width: 90%;
    margin-left: auto;
    margin-right: auto;
  }
}
